<!-- 修改工单 -->
<template>
  <view class="save-tasks">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="修改故障原因"></u-navbar>
    <view class="form-wrapper bg-white margin-normal radius-normal padding-normal">
      <u-form :model="form" ref="uForm" label-width="160">
        <u-form-item label="故障原因" prop="content" :required="true" :borderBottom="false">
          <u-input type="textarea"
                   placeholder="简要描述故障原因（至少 5 个字）"
                   placeholder-style="color: #D4D4D5; line-height: 1; font-size: 14px;"
                   v-model="form.content"/>
        </u-form-item>
      </u-form>
    </view>
    <view class="save-tasks-inner-wrapper margin-normal">
      <u-button v-if="showBtn1" ripple type="success" :loading="loading" @click="saveTasks">确认提交</u-button>
    </view>

    <u-toast ref="uToast"/>

    <u-modal v-model="showModal" @confirm="confirm" @cancel="loading = false" show-cancel-button content="确认提交？"></u-modal>
  </view>
</template>

<script>

  export default {
    name: "midify-task",
    data() {
      return {
        showBtn1: false,
        background: {
          'background': '#f2f2f8'
        },
        showModal: false,
        loading: false,
        form: {
          content: "",
          id: ""
        },
        detail: {},
        rules: {
          content: [
            {
              required: true,
              min: 5,
              message: '简要描述故障原因',
              trigger: 'change'
            }
          ]
        },
        searchParams: {
          id: "",
          status: "",
          type: ""
        }
      }
    },
    computed: {
      PREMISS() {
        return this.$store.state.PREMISS
      }
    },
    onLoad(options) {
      this.searchParams.id = options.taskId

      this.searchParams.status = options.status

      this.searchParams.type = options.type
    },
    onReady() {
      this.$refs.uForm.setRules(this.rules);
      //  修改工单的权限
      const premissObj = uni.getStorageSync("premissObj");
      this.showBtn1 = !!(premissObj && premissObj[this.PREMISS.modifyreason])
      this.getDetail();
    },
    methods: {
      back() {
        const params = this.$u.queryParams({
          taskId: this.detail.id,
          status: this.searchParams.status,
          type: this.searchParams.type
        });
        uni.navigateTo({
          url: "/pages/task-detail/index" + params
        })
      },
      getDetail() {
        this.$u
            .post("/maint/getWeixiuTaskDetail", {
              id: this.searchParams.id
            })
            .then(res => {
              if (res.data) {
                this.detail = res.data;
                this.form.content = this.detail.content;
                this.form.id = this.detail.id;
              } else {
              }
            })
            .catch(err => {
            });
      },
      confirm() {
        this.$u
            .post("/maint/saveTask", this.form)
            .then(res => {
              this.$refs.uToast.show({
                title: res.message,
                type: res.code === 200 ? "success" : "warning"
              });
              if (res.code === 200) {
                this.$store.commit("freshTaskFunc", true);
                this.back();
              }
            })
            .catch(err => {
            }).finally(_ => {
          this.loading = false;
        })
      },
      saveTasks() {
        if (this.loading) {
          return
        }
        this.$refs.uForm.validate(valid => {
          this.showModal = valid;
          this.loading = true;
        });
      },
    }
  }
</script>

<style scoped lang="scss">
  uni-page-body {
    height: 100%;
  }

  .save-tasks {
    height: 100%;
  }

</style>